async function request(URL, options) {
    try {
        const response = await fetch(URL, options);

        if (response.ok === false) {
            const error = await response.json();
            throw new Error(error.message);
        } else {
            try {
                return await response.json();
            } catch (error) {
                return response;
            }
        }
    } catch (error) {
        alert(error.message);
        throw error;
    }
}


function setOptions(method, authToken = undefined, body = undefined) {
    let options = {
        method,
        headers: {}
    }

    if (authToken) {
        options.headers['X-Authorization'] = authToken;
    }

    if (body) {
        options.headers['Content-Type'] = 'application/json';
        options.body = JSON.stringify(body);
    }

    return options;
}

export const baseURL = 'https://cars-react-app-server.herokuapp.com';

async function get(endpoint) {
    return await request(baseURL + endpoint, setOptions('get'));
}

async function post(endpoint, token, data) {
    return await request(baseURL + endpoint, setOptions('post', token, data));
}

async function put(endpoint, token, data) {
    return await request(baseURL + endpoint, setOptions('put', token, data));
}

async function del(endpoint, token) {
    return await request(baseURL + endpoint, setOptions('delete', token));
}

export {
    get,
    post,
    put,
    del
}