import * as CRUD from './API/CRUD';


export async function getAllMoviesArticles() {
    return await CRUD.get('/data/movies');
}

export async function getSpecificMovieArticle(movieId) {
    return await CRUD.get(`/data/movies/${movieId}`);
}

export async function createMovieArticle(title, description, img, token) {
    return await CRUD.post('/data/movies', token, {
        title,
        description,
        img
    });
}

export async function editMovieArticle(title, description, img, movieId, token) {
    return await CRUD.put(`/data/movies/${movieId}`, token, {
        title,
        description,
        img
    });
}

export async function deleteMovieArticle(movieId, token) {
    return await CRUD.del(`/data/movies/${movieId}`, token);
}

export async function likeMovieArticle(movieId, token) {
    return await CRUD.post('/data/likes', token, {
        movieId
    });
}

export async function getNumberOfLikesMovieArticle(movieId) {
    return await CRUD.get(`/data/likes?where=movieId%3D%22${movieId}%22&distinct=_ownerId&count`);
}

export async function getLikeForMovieArticleFromSpecificUser(movieId, userId) {
    return await CRUD.get(`/data/likes?where=movieId%3D%22${movieId}%22%20and%20_ownerId%3D%22${userId}%22`);
}

export async function getMovieArticleBySearchInput(movieTitle) {
    return await CRUD.get(`/data/movies?where=title%3D%22${movieTitle}%22`);
}

export async function getSpecificMovieArticleComments(movieId) {
    return await CRUD.get(`/data/comments?where=movieId%3D%22${movieId}%22`);
}

export async function createNewCommentToAMovieArticle(authorName, content, movieId, token) {
    return await CRUD.post('/data/comments', token, { author: authorName, content, movieId });
}

export async function deleteCommentToAMovieArticle(commentId, token) {
    return await CRUD.del('/data/comments/' + commentId, token);
}