import { Link } from 'react-router-dom';
import style from './MovieCard.module.css';

function MovieCard({
    title,
    img,
    _id
}) {
    return (
        <div className={style.movieCard}>
            <article className="card-header-main">
                <img className={style.movieCardImage} src={img} alt="Movie Card" />
                <h3 className={style.movieCardTitle}>{title}</h3>
            </article>
            <article className="card-footer">
                <Link to={`/movies/details/${_id}`} className={style.movieCardButton}>Details</Link>
            </article>
        </div>
    );
}

export default MovieCard;