import style from '../Decorate.module.css';
import { useContext } from 'react';

import { createMovieArticle } from '../../../../utils/MainRequests';

import useForm from '../../../CustomHooks/useForm';

import * as yup from 'yup';
import { UserContext } from '../../../../Context/UserContext';

function Create({
    history
}) {
    const { currentUserData } = useContext(UserContext);

    const createMovieValidationSchema = yup.object().shape({
        movieTitle: yup.string()
            .matches(/[A-Z][a-z0-9]+/, 'Title should start with uppercase and continue with lowercase and numbers')
            .min(6, 'Movie title should be at least 6 symbols!')
            .max(20, 'Movie title should not be longer than 20 symbols!')
            .required('This field is required!'),
        movieDescription: yup.string().max(100, 'Movie description should not be longer than 100 symbols!'),
    });

    const onCreateMovieArticleSubmitHandler = ({ movieTitle, movieDescription, movieImage }) => {
        (async function createMovie() {
            await createMovieArticle(movieTitle, movieDescription, movieImage, currentUserData.authorizationToken);
            history.push('/');
        })();
    };

    const { handleSubmit, handleChange, handleBlur, errors, values, touched } = useForm({
        movieTitle: '',
        movieDescription: '',
        movieImage: '',
    }, createMovieValidationSchema, false, onCreateMovieArticleSubmitHandler);

    return (
        <section className={style.createPage}>
            <div className={style.createFormWrapper}>
                <h2 className={style.createFormTitle}>CREATE MOVIE ARTICLE</h2>
                <form onSubmit={handleSubmit}>
                    {touched.movieTitle && errors.movieTitle ? <div className="error">{errors.movieTitle}</div> : null}
                    <input className={style.movieTitle}
                        type="text"
                        name="movieTitle"
                        placeholder="ENTER MOVIE TITLE"
                        onChange={handleChange}
                        onBlur={handleBlur}
                        value={values.movieTitle}
                    />
                    {touched.movieDescription && errors.movieDescription ? <div className="error">{errors.movieDescription}</div> : null}
                    <textarea className={style.movieDescription}
                        type="text"
                        name="movieDescription"
                        placeholder="ENTER MOVIE DESCRIPTION"
                        rows="4"
                        columns="50"
                        onChange={handleChange}
                        onBlur={handleBlur}
                        value={values.movieDescription}
                    >
                    </textarea>
                    {touched.movieImage && errors.movieImage ? <div className="error">{errors.movieImage}</div> : null}
                    <input className={style.movieImageURL}
                        type="text"
                        name="movieImage"
                        placeholder="ENTER MOVIE IMAGE URL"
                        onChange={handleChange}
                        onBlur={handleBlur}
                        value={values.movieImage}
                    />
                    <button className={style.createMovieBtn} type="submit">CREATE</button>
                </form>
            </div>
        </section>
    );
}

export default Create;