import style from './MoviesForum.module.css';

function MoviesForum() {
    return (
        <section>
            <article className={style.homePage}>
                <img className={style.moviesBackgroundImage} src="https://slicksmovieblog.files.wordpress.com/2014/08/cropped-movie-banner-e1408372575210.jpg" alt="Movie" />
                <h2 className={style.moviesForumTitle}>Movies Forum</h2>
                <p className={style.moviesForumDescription}>Search for newest movies, post new movies, share opinions and likes anywhere, anytime!</p>
            </article>
        </section>
    );
}

export default MoviesForum;