import { useState, useEffect } from 'react';

import MoviesForum from './MoviesForum/MoviesForum';
import MovieCard from './MovieCard/MovieCard';
import { getAllMoviesArticles } from '../../../utils/MainRequests';
import Loader from '../../Common/Loader/Loader';

function Home() {
    const [movies, setMovies] = useState([]);
    const [isLoading, setIsLoading] = useState(true);

    useEffect(() => {
        (async function getMovies() {
            const movieArticles = await getAllMoviesArticles();
            setMovies(movieArticles);
            setIsLoading(false);
        })();
    }, []);
    return (
        <>
            <MoviesForum />
            <section>
                <div>
                    <h1 style={{
                        fontSize: 35,
                        fontWeight: 500,
                        fontFamily: 'Courier New',
                        textAlign: 'center',
                        marginTop: 40
                    }}>Movie Articles</h1>
                    <div className="card-wrapper">
                        {
                            isLoading ? <Loader /> : movies.map((movieProps, keyIndex) => <MovieCard key={keyIndex} {...movieProps} />)
                        }
                    </div>
                </div>
            </section>
        </>
    );
}

export default Home;